const vuetify = new Vuetify({
  rtl: false,
  theme: {
    dark: true,
    themes: {
      dark: {
        primary: '#9a8866',
        anchor: '#9a8866',
        accent: '#FF4081',
        secondary: '#ffe18d',
        success: '#4CAF50',
        info: '#2196F3',
        warning: '#FB8C00',
        error: '#FF5252',
      },
      light: {
        primary: '#1976D2',
        accent: '#e91e63',
        secondary: '#30b1dc',
        success: '#4CAF50',
        info: '#2196F3',
        warning: '#FB8C00',
        error: '#FF5252'
      }
    }
  }
}
)

const store = new Vuex.Store({
  state: {
    doneQuests: [],
    doneHideout: [],
    stocked: {},
    config: {},
    count: 0,
    levels: {},
    money: {}
  },
  mutations: {
    setMoney(state, money) {
      state.money = money;
    },
    setDoneQuests(state, doneQuests) {
      state.doneQuests = doneQuests;
    },
    setDoneHideout(state, doneHideout) {
      state.doneHideout = doneHideout;
    },
    setStocked(state, stocked) {
      state.stocked = stocked;
    },
    setShowHideout(state, showHideout) {
      state.config.showHideout = showHideout;
    },
    setShowQuests(state, showQuests) {
      state.config.showQuests = showQuests;
    },
    setShowNonPickupQuests(state, showNonPickupQuests) {
      state.config.showNonPickupQuests = showNonPickupQuests;
    },
    setShowQuestItems(state, showQuestItems) {
      state.config.showQuestItems = showQuestItems;
    },
    setShowQuestPickups(state, showQuestPickups) {
      state.config.showQuestPickups = showQuestPickups;
    },
    setShowHideoutPickups(state, showHideoutPickups) {
      state.config.showHideoutPickups = showHideoutPickups;
    },
    setHideNonFoundInRaid(state, hideNonFoundInRaid) {
      state.config.hideNonFoundInRaid = hideNonFoundInRaid;
    },
    setLookAheadQuest(state, level) {
      state.config.lookAheadQuest = level;
    },
    setLookAheadLevel(state, level) {
      state.config.lookAheadLevel = level;
    },
    setShowOptionsQuests(state, v) {
      state.showOptionsQuests = v;
    },
    setShowOptionsHideout(state, v) {
      state.showOptionsHideout = v;
    },
    setShowOptionsChecklist(state, v) {
      state.showOptionsChecklist = v;
    },
    setShowOptionsCharacter(state, v) {
      state.showOptionsCharacter = v;
    },
    setConfig(state, config) {
      state.config = config;
    },
    removeItemsFromStock(state, items) {
      Object.keys(items).forEach(function (key) {
        console.log(key);
        if (!state.stocked[key]) state.stocked[key] = { nonraid: 0, raid: 0 }
        console.log("state.stocked", state.stocked[key].nonraid, state.stocked[key].raid)
        console.log("items", items[key].nonraid ? items[key].nonraid.amount : 0, items[key].raid ? items[key].raid.amount : 0)
        if (items[key].nonraid) state.stocked[key].nonraid = state.stocked[key].nonraid ? state.stocked[key].nonraid > items[key].nonraid.amount ? state.stocked[key].nonraid - items[key].nonraid.amount : 0 : 0;
        if (items[key].raid) state.stocked[key].raid = state.stocked[key].raid ? state.stocked[key].raid > items[key].raid ? state.stocked[key].raid - items[key].raid : 0 : 0;
        console.log("state.stocked", state.stocked[key].nonraid, state.stocked[key].raid)
      });
    },
    setLevels(state, levels) {
      state.levels = levels;
    },

  }
});

Vue.mixin({
  data: () => ({
    staticData: staticData,
  }),
  filters: {
    resolveQuest(id) {
      if (id && id !== "") {
        return staticData.getQuest(id);
      }
      else return {};
    },
    resolveHideout(id) {
      if (id && id !== "") {
        return staticData.getHideout(id);
      }
      else return {};
    },
    name(object) {
      return object.name;
    }
  }
})


new Vue({
  vuetify,
  store,
  router,
  render: h => h(App)
}).$mount('#app')
