const Hideout = resolve => {
    require.ensure(['./components/Hideout.vue'], () => {
        resolve(require('./components/Hideout.vue'));
    }, 'hideout');
};

const Info = resolve => {
    require.ensure(['./components/Info.vue'], () => {
        resolve(require('./components/Info.vue'));
    }, 'info');
};

const Quests = resolve => {
    require.ensure(['./components/Quests.vue'], () => {
        resolve(require('./components/Quests.vue'));
    }, 'quests');
};

const Checklist = resolve => {
    require.ensure(['./components/Checklist.vue'], () => {
        resolve(require('./components/Checklist.vue'));
    }, 'checklist');
};

const Character = resolve => {
    require.ensure(['./components/Character.vue'], () => {
        resolve(require('./components/Character.vue'));
    }, 'character');
};


export const routes = [{
    path: '/info',
    alias: '',
    name: 'info',
    components: {
        default: Info
    }
}, {
    path: '/hideout',
    name: 'hideout',
    components: {
        default: Hideout
    }
}, {
    path: '/quests',
    name: 'quests',
    components: {
        default: Quests
    }
}, {
    path: '/checklist',
    name: 'checklist',
    components: {
        default: Checklist
    }
}, {
    path: '/character',
    name: 'character',
    components: {
        default: Character
    }
}];